body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

.App {
  height: 100vh;
  width: 100vw;
  display: grid;
  grid-template-columns: repeat(13, 8vw);
  grid-template-rows: repeat(13, 8vh); }
  .App .App-header {
    background-color: #282c34;
    min-height: 10vh;
    display: inline-block;
    font-size: calc(10px + 2vmin);
    color: white;
    grid-column: 1 / 14;
    grid-row: 1 / 2; }
    .App .App-header .logo {
      max-height: 10vh; }
  .App .myName {
    grid-column: 1 / 14;
    grid-row: 3 / 5;
    text-align: center; }
  .App .carousel {
    z-index: 5;
    grid-column: 7 / 12;
    grid-row: 8 / 11;
    margin: auto 0; }
    .App .carousel .productImage {
      margin: auto; }
    .App .carousel .imageBackground {
      border-radius: 25px;
      background-color: yellow;
      box-shadow: 3px 3px 3px #282c34;
      width: 170px;
      margin-left: 10px; }
  .App .carouselBar {
    z-index: 1;
    height: 8vh;
    grid-column: 2 / 13;
    grid-row: 9 / 11;
    background: #61dafb;
    text-align: left;
    padding-left: 4rem;
    box-shadow: 3px 3px 3px #282c34; }
    .App .carouselBar .text {
      line-height: 8vh; }
  .App .footer {
    grid-column: 1 / 13;
    grid-row: 12/ 13; }

